import React from 'react';
import { Switch, Route } from 'react-router-dom';

import { HomePage } from '../pages/HomePage';
import { SobrePage } from '../pages/SobrePage';
import { PostsPage } from '../pages/PostsPage';
import { PostPage } from '../pages/PostPage';
import { Error404 } from '../pages/Error404';
import { ContatoPage } from '../pages/ContatoPage';

const Routes: React.FC = (): JSX.Element => {
  return (
    <Switch>
      <Route path="/ublog" component={HomePage} exact />
      <Route path="/ublog/sobre-a-ublog" component={SobrePage} />
      <Route path="/ublog/posts" component={PostsPage} exact />
      <Route path="/ublog/posts/:title" component={PostPage} />
      <Route path="/ublog/contato" component={ContatoPage} />
      <Route path="*" component={Error404} />
    </Switch>
  );
};

export { Routes };
